// Styles for the posts page (or projects page)
#post-container {
    display: flex;
    flex-wrap: wrap 
}
.post {
    margin-left: auto; 
    margin-right: auto;
    width: calc(50% - 4em);
    min-width: 15em;
    height: 15em;
    padding:0;
    margin-top: 1em;
    margin-bottom: 1em;
    border: 1px solid #aaa;
    border-radius:2px;
    background-color: #fff;
    overflow: hidden;
    transition: box-shadow .3s;
    box-sizing: border-box;
    
}
.post > a {
    text-decoration: none;
    color: #222;
    padding:0;
    display: block;
    width: 100%;
    height: 100%;
    transition: transform .3s;
}

.post > a > div.post-info {
    width:100%;
    height: 100%;
    
    padding: 1em;
    background-color: rgb(233, 233, 233);
    transition: all .3s;
    box-sizing: border-box;
}

.post > a > div.header-image {
    background-size:cover;
    background-position: center center;
    height:60%;
    transition: all .3s;
    
}
.post > a:hover {
    transform: translateY(-5em);
    transition: all .3s;
    
}
