.post:hover {
    box-shadow:  0 2px 4px -1px rgba(0,0,0,0.5);
    transition: box-shadow .3s;
}

#headerimage {
    background-size: cover;
    z-index: -3;
    position: relative;
    top: -5em;
    width: 100%;
    height: 20em;
    margin-bottom: -4em;
    border-bottom: 1px solid #999;
    background-position: center center;
    background-repeat: no-repeat;
}
#headerimage > div {
    color: #fff;
    position: relative;
    top: 15em;
    margin-left:auto;
    margin-right: auto;
}

// for small devices
@media (max-width: 800px) {
    .post {
        
        width: calc(100% - 4em);
        height: 25em;
    }
}
