@import url('https://fonts.googleapis.com/css2?family=Roboto&family=Source+Sans+Pro:wght@600&display=swap');

$maincolor: #FFB81C;
$maincolorDarker: #EEA400;
$secondcolor: #003594;
$secondcolorDarker: #00215C;
$secondcolorLighter: #3e68b4;
html,
body {
   margin:0;
   padding:0;
   height:100%;
}
#main {
min-height:100%;
   position:relative;
}

#content li {
    font-size:1.8em;
}

#content p {
    font-size:1.3em;
}
#content {
    padding-bottom: 6.3em;
}
code {
    background: rgba(0,0,0,0);
    border: none;
}
pre {
    border-radius: 3px;
}


h1,h2,h3,h4,h5,h6 {
    font-family: 'Source Sans Pro', sans-serif;
}
body {
    font-family: 'Roboto', sans-serif;
}

nav {
    background-color: $maincolor;
    padding: .5em 1em .5em 2em;
    height: 3em;
    display: inline-block; 
    white-space: nowrap;
}

nav a.button {
    margin-right: 1em;
    z-index:1;
    font-size: .8em;
    letter-spacing: .4rem;
}

.logo {
    width: 20em;
    background-image: url(/assets/images/logo.svg);
    border: 0;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center center;
    
}

#nav-top {
    background-color: $maincolor;
    width: 100%;
    height: .5em
}
#nav-slant {
    background-color:$maincolor;
    width:6em;
    height:5em;
    transform: skew(-45deg) scaleX(1.5) translateX(-4em) translateY(-1em);
    display: inline-block;
    z-index: -1;
    position:absolute;
}

#navbar-container {
    overflow:hidden;
    height: 4em;
    margin-bottom: 1em;
}

footer {
    background-color: $secondcolor;
    height:100%;
    display: inline-block;
    color: #fff;
    padding-top:.8em;
    padding-bottom: .5em;
    padding-left: 3em;
    padding-right: 1em;
    text-align:right;
}

#foot-slant { 
    background-color:$secondcolor;
    width:4em;
    height:4em;
    transform: skew(-45deg) translateX(1em) translateY(-1em);
    display: inline-block;
    z-index: -1;
}
#footer-container {
    width:100%;
    height: 2.8em;
    overflow-y: hidden;
    text-align: right;
}
#footer-bottom {
    
    background-color: $secondcolor;
    width: 100%;
    height: .5em
}
#whole-footer-container {
    margin-top: 3em;
    height: 3.3em;
    position:absolute;
    bottom:0;
    width:100%;
}
.footer-social-icon {
    height: 2em;
    vertical-align: middle;
    padding-left: 5px;
}
 svg {
    fill: #fff;
}
.button.button-primary.button-selected {
    border-color: $secondcolorDarker;
    box-shadow: inset 0px 0px 2px 1px $secondcolorDarker ;
}

// Overrides for Skeleton
.button.button-primary,
button.button-primary,
input[type="submit"].button-primary,
input[type="reset"].button-primary,
input[type="button"].button-primary {
  color: #FFF;
  background-color: $secondcolor;
  border-color: $secondcolor;
border-radius: 0px; }

.button.button-primary:hover,
button.button-primary:hover,
input[type="submit"].button-primary:hover,
input[type="reset"].button-primary:hover,
input[type="button"].button-primary:hover,
.button.button-primary:focus,
button.button-primary:focus,
input[type="submit"].button-primary:focus,
input[type="reset"].button-primary:focus,
input[type="button"].button-primary:focus {
  color: #FFF;
  background-color: $secondcolorDarker;
  border-color: $secondcolorDarker; }


@media (max-width: 800px) {
    #nav-slant {
        display: none;
    }
    
    #navbar-container {
        overflow: visible;
        height: auto;
        margin-bottom: 1em;
    }
    
    nav {
    
        padding: .5em 1em .5em 2em;
        height: auto;
        width:100%;
        box-sizing: border-box;
        white-space: normal;
        
    }

    nav a.button-primary {
        width: 90%;
        margin-left: auto;
        margin-right: auto;
    }
    .logo {
        margin-left:auto;
        margin-right: auto;
        margin-bottom: 1em;
        width: 90%;
    }

    #foot-slant {
        display: none;
    }

    #whole-footer-container {
        overflow: visible;
        height: auto;
        margin-top: 1em;
    }
    footer {
        width:100%;
        text-align: center;
        padding-left: 0;
        padding-right: 0;
        overflow: visible;
    }
    #footer-container {
        height: auto;
        overflow-y: visible;
        text-align: center;
    }
    
}